@tailwind base;
@tailwind components;
@tailwind utilities;

html { /* font-size:10px; */ } 
body { margin:0 auto; padding:0; overflow-x: hidden; font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif; line-height: 1.5em; font-size: 16px;}
html, h1, h2, h3, h4, h5, h6, form, fieldset, img { margin:0; padding:0; border:0 } 
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display:block } 

ul, dl,dt,dd { margin:0; padding:0; list-style:none } 
legend { position:absolute; margin:0; padding:0; font-size:0; line-height:0; text-indent:-9999em; overflow:hidden } 
label, input, button, select, img { vertical-align:middle; } 
input, button { margin:0; padding:0;} 
input[type="submit"] { cursor:pointer } 
button { cursor:pointer } 
select { margin:0 } 
p { margin:0; padding:0; word-break:keep-all; } 
a { text-decoration:none } 
a:hover { text-decoration: none; } 
.un_reboot_a { color: var(--main); text-decoration: underline; } 
*, :after, :before { 
 -webkit-box-sizing:border-box; 
 -moz-box-sizing:border-box; 
 box-sizing:border-box; 
 word-break: keep-all; 
 } 

img { max-width:100%; display:inline-block; height: auto; } 

/*글자 줄임말*/
.line_text { white-space: nowrap; overflow: hidden; text-overflow: ellipsis; word-break: break-all; } 
.line1_text { 
 white-space: normal; 
 overflow: hidden; 
 text-overflow: ellipsis;
 word-wrap: break-word; 
 display: -webkit-box; 
 -webkit-line-clamp: 1; 
 -webkit-box-orient: vertical; 
 } 
.line2_text { 
white-space: normal; 
overflow: hidden; 
text-overflow: ellipsis;
word-wrap: break-word; 
display: -webkit-box; 
-webkit-line-clamp: 2; 
-webkit-box-orient: vertical; 

 } 
.line3_text { 
white-space: normal; 
overflow: hidden; 
text-overflow: ellipsis;
text-align: left; 
word-wrap: break-word; 
display: -webkit-box; 
-webkit-line-clamp: 3; 
-webkit-box-orient: vertical; 

 } 

